import React from "react"
import { PageProps, Link, graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

type DataProps = {
  site: {
    buildTime: string
  }
}

const Uses: React.FC<PageProps<DataProps>> = ({
                                                data,
                                                path,
                                                location
                                              }) => (
  <Layout title="Uses" location={location}>
    <SEO title="Uses" />
    <h1>What does Dmytro use for work and recreation?</h1>
    <h2>Home Office (I work from home now, as it's COVID-19 time)</h2>
    <p>
      For my work I'm using a 2018 MacBook Pro 15" , 16GB RAM, 512GB SSD <br />and 2 x 21.5" FullHD Dell monitors
      (P2219H) on a dual monitor stand to free up some table surface.
    </p>
    <p>
      I'm not using any external keyboards or mice with MacBook as I still think that Mac's trackpad is amazing input
      device, and I can tolerate Butterfly keyboard.
    </p>
    <h2>Work Office</h2>
    <p>The same laptop, but better monitors - some 27" QHD Dell monitors and Dell docking station for Ethernet and USB-c
      charging.</p>
    <h2>
      Home, non-work environment
    </h2>
    <p>
      The same monitors as in a home office, but two different OS setups with dedicated hardware: Mac and Windows
    </p>
    <h3>Mac environment</h3>
    <p>
      2019 MacBook Pro 16", 64GB RAM, 1TB SSD
    </p>
    <h3>Windows environment</h3>
    <p>
      Dell Latitude e7450, 16GB RAM, 256GB SSD
    </p>
    <p>
      Logitech G900 wireless mouse, <br />
      Keychron K2 wireless keyboard
    </p>
    <p>

      This page was last edited at{" "}
      {data.site.buildTime}.
    </p>
    <Link to="/">Go back to the homepage</Link>
  </Layout>
)

export default Uses

export const query = graphql`
    {
        site {
            buildTime(formatString: "YYYY-MM-DD hh:mm a z")
        }
    }
`
